// =============================================================================
// Billing Report API
// =============================================================================
// GET /api/v1/billing/report
// 
// Generate revenue and usage reports for billing periods

import { NextRequest, NextResponse } from 'next/server';
import { requireAuth } from '@/lib/auth';
import { extractRequestContext, wrapResponse, createErrorResponse } from '@/lib/request-logging';
import { generateRevenueReport, getBillingPeriod, type BillingPeriod } from '@/lib/billing-tracker';

/**
 * GET /api/v1/billing/report
 * 
 * Generate revenue report for a billing period
 * 
 * Query parameters:
 * - startDate: ISO date string (optional, defaults to start of current period)
 * - endDate: ISO date string (optional, defaults to end of current period)
 * - periodType: 'daily' | 'weekly' | 'monthly' (default: 'monthly')
 * - endpointIds: Comma-separated list of endpoint IDs (optional, all endpoints if not provided)
 */
export async function GET(request: NextRequest) {
  const ctx = await extractRequestContext(request, '/api/v1/billing/report');
  
  try {
    // Require admin authentication for billing reports
    const authResult = await requireAuth(request, 'admin');
    if ('response' in authResult) {
      return wrapResponse(authResult.response, ctx);
    }
    
    ctx.apiKeyId = authResult.apiKey.id;
    
    // Parse query parameters
    const searchParams = request.nextUrl.searchParams;
    const periodType = (searchParams.get('periodType') || 'monthly') as 'daily' | 'weekly' | 'monthly';
    const startDate = searchParams.get('startDate');
    const endDate = searchParams.get('endDate');
    const endpointIdsParam = searchParams.get('endpointIds');
    const endpointIds = endpointIdsParam ? endpointIdsParam.split(',').map(id => id.trim()) : undefined;
    
    // Determine billing period
    let period: BillingPeriod;
    if (startDate && endDate) {
      period = {
        startDate,
        endDate,
        periodType: 'custom',
      };
    } else {
      const date = startDate ? new Date(startDate) : new Date();
      period = getBillingPeriod(date, periodType);
    }
    
    // Generate report
    const report = await generateRevenueReport(period, endpointIds);
    
    return NextResponse.json(report);
  } catch (error) {
    return createErrorResponse(
      ctx,
      error instanceof Error ? error : new Error('Internal server error'),
      500,
      'INTERNAL_ERROR'
    );
  }
}






