// =============================================================================
// Billing and Usage Tracking Integration Layer
// =============================================================================
// Provides billing period tracking, usage aggregation, and revenue reporting
// for metered endpoints

import { getDb } from '@/db/client';
import { getUsageStats, listUsageLogs, getPaymentsForEndpoint } from '@/db/metered-endpoints';
import { getX402CallLogs } from '@/integrations/x402/x402-call-log';
import { createLogger } from './logger';

const logger = createLogger({ component: 'BillingTracker' });

export interface BillingPeriod {
  startDate: string;
  endDate: string;
  periodType: 'daily' | 'weekly' | 'monthly' | 'custom';
}

export interface UsageAggregation {
  endpointId: string;
  period: BillingPeriod;
  totalRequests: number;
  totalPayments: number;
  totalRevenue: string; // In atomic units
  totalUnits: number; // Usage units (could be different from requests)
  averageResponseTime: number;
  successRate: number;
  errorRate: number;
  breakdown: {
    byStatus: Record<string, number>;
    byFacilitator: Record<string, number>;
    byErrorCode: Record<string, number>;
  };
}

export interface RevenueReport {
  period: BillingPeriod;
  totalRevenue: string;
  totalPayments: number;
  totalRequests: number;
  byEndpoint: Array<{
    endpointId: string;
    endpointName: string;
    revenue: string;
    payments: number;
    requests: number;
  }>;
  byFacilitator: Record<string, {
    revenue: string;
    payments: number;
  }>;
  trends: {
    dailyRevenue: Array<{ date: string; revenue: string; payments: number }>;
  };
}

export interface CustomerUsage {
  customerId: string; // API key ID or user ID
  period: BillingPeriod;
  endpoints: Array<{
    endpointId: string;
    endpointName: string;
    usage: UsageAggregation;
  }>;
  totalUsage: {
    totalRequests: number;
    totalPayments: number;
    totalRevenue: string;
    totalUnits: number;
  };
}

/**
 * Get billing period dates
 */
export function getBillingPeriod(
  date: Date = new Date(),
  periodType: 'daily' | 'weekly' | 'monthly' = 'monthly'
): BillingPeriod {
  const start = new Date(date);
  const end = new Date(date);

  switch (periodType) {
    case 'daily':
      start.setHours(0, 0, 0, 0);
      end.setHours(23, 59, 59, 999);
      break;
    
    case 'weekly':
      // Start of week (Sunday)
      const day = start.getDay();
      start.setDate(start.getDate() - day);
      start.setHours(0, 0, 0, 0);
      end.setDate(start.getDate() + 6);
      end.setHours(23, 59, 59, 999);
      break;
    
    case 'monthly':
      start.setDate(1);
      start.setHours(0, 0, 0, 0);
      end.setMonth(end.getMonth() + 1);
      end.setDate(0); // Last day of month
      end.setHours(23, 59, 59, 999);
      break;
  }

  return {
    startDate: start.toISOString(),
    endDate: end.toISOString(),
    periodType,
  };
}

/**
 * Aggregate usage for a specific endpoint and billing period
 */
export async function aggregateUsageForPeriod(
  endpointId: string,
  period: BillingPeriod
): Promise<UsageAggregation> {
  try {
    // Get usage stats
    const stats = await getUsageStats(endpointId, period.startDate, period.endDate);
    
    // Get x402 call logs for detailed breakdown
    const logs = await getX402CallLogs(endpointId, {
      startDate: period.startDate,
      endDate: period.endDate,
      limit: 10000, // Get enough for aggregation
    });
    
    // Get payments
    const payments = await getPaymentsForEndpoint(endpointId, 10000);
    const periodPayments = payments.filter(
      p => p.verifiedAt >= period.startDate && p.verifiedAt <= period.endDate
    );
    
    // Calculate breakdowns
    const byStatus: Record<string, number> = {};
    const byFacilitator: Record<string, number> = {};
    const byErrorCode: Record<string, number> = {};
    
    let totalUnits = 0;
    let successCount = 0;
    let errorCount = 0;
    
    for (const log of logs) {
      byStatus[log.status] = (byStatus[log.status] || 0) + 1;
      byFacilitator[log.facilitator || 'UNKNOWN'] = (byFacilitator[log.facilitator || 'UNKNOWN'] || 0) + 1;
      
      if (log.status === 'success') {
        successCount++;
        totalUnits += 1; // 1 unit per successful request
      } else {
        errorCount++;
        if (log.errorCode) {
          byErrorCode[log.errorCode] = (byErrorCode[log.errorCode] || 0) + 1;
        }
      }
    }
    
    const totalRequests = logs.length;
    const successRate = totalRequests > 0 ? successCount / totalRequests : 0;
    const errorRate = totalRequests > 0 ? errorCount / totalRequests : 0;
    
    return {
      endpointId,
      period,
      totalRequests: stats.totalRequests,
      totalPayments: stats.totalPayments,
      totalRevenue: stats.totalRevenue,
      totalUnits,
      averageResponseTime: stats.averageResponseTime,
      successRate,
      errorRate,
      breakdown: {
        byStatus,
        byFacilitator,
        byErrorCode,
      },
    };
  } catch (error) {
      logger.error({ endpointId, period, error }, 'Failed to aggregate usage');
    throw error;
  }
}

/**
 * Generate revenue report for a billing period
 */
export async function generateRevenueReport(
  period: BillingPeriod,
  endpointIds?: string[]
): Promise<RevenueReport> {
  try {
    const db = getDb();
    
    // Get all endpoints or filter by provided IDs
    let endpoints: any[] = [];
    if (endpointIds && endpointIds.length > 0) {
      endpoints = await Promise.all(
        endpointIds.map(id => db.getEndpoint(id))
      );
      endpoints = endpoints.filter(e => e !== null);
    } else {
      endpoints = await db.listEndpoints({ status: 'active' });
    }
    
    // Aggregate usage for each endpoint
    const byEndpoint: Array<{
      endpointId: string;
      endpointName: string;
      revenue: string;
      payments: number;
      requests: number;
    }> = [];
    
    const byFacilitator: Record<string, { revenue: string; payments: number }> = {};
    
    let totalRevenue = BigInt(0);
    let totalPayments = 0;
    let totalRequests = 0;
    
    for (const endpoint of endpoints) {
      const usage = await aggregateUsageForPeriod(endpoint.id, period);
      
      byEndpoint.push({
        endpointId: endpoint.id,
        endpointName: endpoint.name,
        revenue: usage.totalRevenue,
        payments: usage.totalPayments,
        requests: usage.totalRequests,
      });
      
      totalRevenue += BigInt(usage.totalRevenue);
      totalPayments += usage.totalPayments;
      totalRequests += usage.totalRequests;
      
      // Aggregate by facilitator
      for (const [facilitator, count] of Object.entries(usage.breakdown.byFacilitator)) {
        if (!byFacilitator[facilitator]) {
          byFacilitator[facilitator] = { revenue: '0', payments: 0 };
        }
        // Estimate revenue per facilitator (simplified - could be more accurate)
        const facilitatorRevenue = BigInt(usage.totalRevenue) / BigInt(usage.totalPayments || 1) * BigInt(count);
        byFacilitator[facilitator].revenue = (
          BigInt(byFacilitator[facilitator].revenue) + facilitatorRevenue
        ).toString();
        byFacilitator[facilitator].payments += count;
      }
    }
    
    // Generate daily trends
    const dailyRevenue: Array<{ date: string; revenue: string; payments: number }> = [];
    const start = new Date(period.startDate);
    const end = new Date(period.endDate);
    
    for (let d = new Date(start); d <= end; d.setDate(d.getDate() + 1)) {
      const dayStart = new Date(d);
      dayStart.setHours(0, 0, 0, 0);
      const dayEnd = new Date(d);
      dayEnd.setHours(23, 59, 59, 999);
      
      const dayPeriod: BillingPeriod = {
        startDate: dayStart.toISOString(),
        endDate: dayEnd.toISOString(),
        periodType: 'daily',
      };
      
      let dayRevenue = BigInt(0);
      let dayPayments = 0;
      
      for (const endpoint of endpoints) {
        const dayStats = await getUsageStats(
          endpoint.id,
          dayPeriod.startDate,
          dayPeriod.endDate
        );
        dayRevenue += BigInt(dayStats.totalRevenue);
        dayPayments += dayStats.totalPayments;
      }
      
      dailyRevenue.push({
        date: d.toISOString().split('T')[0],
        revenue: dayRevenue.toString(),
        payments: dayPayments,
      });
    }
    
    return {
      period,
      totalRevenue: totalRevenue.toString(),
      totalPayments,
      totalRequests,
      byEndpoint,
      byFacilitator,
      trends: {
        dailyRevenue,
      },
    };
  } catch (error) {
      logger.error({ period, error }, 'Failed to generate revenue report');
    throw error;
  }
}

/**
 * Get customer usage for a billing period
 */
export async function getCustomerUsage(
  customerId: string, // API key ID
  period: BillingPeriod
): Promise<CustomerUsage> {
  try {
    const db = getDb();
    
    // Get all endpoints (for now, we don't track ownership - could add later)
    // In a real system, you'd filter by customer/API key ownership
    const allEndpoints = await db.listEndpoints({ status: 'active' });
    
    // For now, aggregate usage across all endpoints
    // TODO: Filter by customer ownership when that's implemented
    const endpoints: Array<{
      endpointId: string;
      endpointName: string;
      usage: UsageAggregation;
    }> = [];
    
    let totalRequests = 0;
    let totalPayments = 0;
    let totalRevenue = BigInt(0);
    let totalUnits = 0;
    
    for (const endpoint of allEndpoints) {
      const usage = await aggregateUsageForPeriod(endpoint.id, period);
      
      endpoints.push({
        endpointId: endpoint.id,
        endpointName: endpoint.name,
        usage,
      });
      
      totalRequests += usage.totalRequests;
      totalPayments += usage.totalPayments;
      totalRevenue += BigInt(usage.totalRevenue);
      totalUnits += usage.totalUnits;
    }
    
    return {
      customerId,
      period,
      endpoints,
      totalUsage: {
        totalRequests,
        totalPayments,
        totalRevenue: totalRevenue.toString(),
        totalUnits,
      },
    };
  } catch (error) {
      logger.error({ customerId, period, error }, 'Failed to get customer usage');
    throw error;
  }
}

/**
 * Calculate invoice data for a customer
 */
export interface InvoiceData {
  customerId: string;
  period: BillingPeriod;
  lineItems: Array<{
    endpointId: string;
    endpointName: string;
    description: string;
    quantity: number; // Units
    unitPrice: string; // Price per unit in atomic units
    total: string; // Total in atomic units
  }>;
  subtotal: string;
  tax: string; // Could be calculated if needed
  total: string;
  currency: string;
  network: string;
}

export async function calculateInvoice(
  customerId: string,
  period: BillingPeriod
): Promise<InvoiceData> {
  try {
    const customerUsage = await getCustomerUsage(customerId, period);
    
    const lineItems: InvoiceData['lineItems'] = [];
    let subtotal = BigInt(0);
    
    for (const endpoint of customerUsage.endpoints) {
      if (endpoint.usage.totalUnits > 0) {
        // Get endpoint to get price
        const endpointData = await getDb().getEndpoint(endpoint.endpointId);
        if (endpointData) {
          const unitPrice = endpointData.price; // Price per unit
          const total = BigInt(unitPrice) * BigInt(endpoint.usage.totalUnits);
          
          lineItems.push({
            endpointId: endpoint.endpointId,
            endpointName: endpoint.endpointName,
            description: `Usage for ${endpoint.endpointName}`,
            quantity: endpoint.usage.totalUnits,
            unitPrice,
            total: total.toString(),
          });
          
          subtotal += total;
        }
      }
    }
    
    // For now, no tax calculation
    const tax = BigInt(0);
    const total = subtotal + tax;
    
    return {
      customerId,
      period,
      lineItems,
      subtotal: subtotal.toString(),
      tax: tax.toString(),
      total: total.toString(),
      currency: 'USDC', // Default
      network: 'base', // Default
    };
  } catch (error) {
      logger.error({ customerId, period, error }, 'Failed to calculate invoice');
    throw error;
  }
}

